*{
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
}
.main-div {
  width: 100%;
  height: 100vh;
  background: linear-gradient(45deg, #6f2121, #3f51b5);
  display: flex;
  justify-content: center;
}
figcaption {
  color: #fff;
  letter-spacing: 1px;
  font-size: 20px;
  text-align: center;
  margin: 10px;
}

.addItems {
  color: #000;
  padding: 2px;
  border-radius: 5px;
  margin: 0 0 30px;
  display: flex;
  align-items: center;
  background: #fff;
}
input {
  background: white;
  margin: 0;
  padding: 7px 8px;
  font-size: 14px;
  color: #000;
  border: 1px solid white;
  border-radius: inherit;
  padding-right: 80px;
  margin-right: 9px;
}
input::placeholder {
  color: #bbb;
}
input[type="search"]:focus {
  box-shadow: 0 0 3px 0 #1183d6;
  border-color: #1183d6;
  outline: none;
}
.eachItem{
  background: #01006f;
  padding: 5px 12px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  color: #fff;
}
.eachItem h3{    
  width: 255px;
}
.fa-plus{
  color: #1700ff;
  cursor: pointer;
}
.fa-trash-alt{
  color: red;
  cursor: pointer;
}
.clearItem{
  padding-left: 37px;
}
.clearItem button {
  text-decoration: none;
  transition: all 0.45s ease-in-out !important;
  margin: 30px;
  z-index: 1;
}

.clearItem button:hover {
  transition: all 0.45s ease-in-out !important;
}
.shineli-btn {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 150px;
  height: 40px;
  background: transparent;
  border: 1px solid transparent;
  color: white;
  text-transform: uppercase;
  overflow: hidden;
  letter-spacing: 2px;
  text-shadow: 1px 1px 0px red;
  box-shadow: #001cd0 -1px 1px, #001cd0 -1px -1px, #001cd0 1px -1px, #0015ff 5px 5px;
}

.shineli-btn:hover {
  letter-spacing: 4px;
  width: 170px;
  text-decoration: 2px dashed underline red;
  padding: 5px 50px 0px 0px;
  box-shadow: #0022ff 5px 0px, #0022ff 5px -5px, #001cd0 0px 0px, #001cd0 5px 0px;
}

.shineli-btn::after {
  content: "";
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  top: 0;
  left: 0;
  width: 400%;
  height: 100%;
  background: linear-gradient(
    68deg,
    rgba(0, 0, 0, 0) 0px,
    rgba(0, 0, 0, 0) 20px,
    #0017ff 20px,
    #0600a1 50px,
    rgba(0, 0, 0, 0) 90px,
    rgba(0, 0, 0, 0) 90px
  );
  background-size: 100%;
  background-repeat: repeat;
  z-index: -1;
  border: 11px solid transparent;
  transition: all 0.6s ease-in-out;
}

.shineli-btn:hover.shineli-btn::after {
  background-size: 10%;
  background-position: 20%;
  transition: all 0.6s ease-in-out;
  transform: scale(2);
}

